import React, { Component } from "react";
import ReactPlayer from "react-player";
import "./responsive-player.css";
import PlayCircleOutlineIcon from "@mui/icons-material/PlayCircleOutline";

class ResponsivePlayer extends Component {
  state = {
    url: null,
    pip: false,
    playing: true,
    controls: false,
    light: false,
    volume: 0.8,
    muted: false,
    played: 0,
    loaded: 0,
    duration: 0,
    playbackRate: 1.0,
    loop: false,
    seconds: 0,
  };

  handleProgress = (secs) => {
    console.log("onProgress", secs);

    this.setState({ seconds: secs });
    console.log("secs", this.state.seconds);
    if (parseFloat(secs) > 10 && parseFloat(secs) < 11) {
      this.setState({ playing: false });
    }

    // We only want to update time slider if we are not currently seeking
  };

  handlePlay = () => {
    console.log("onPlay");
    this.setState({ playing: true });
  };

  handlePause = () => {
    console.log("onPause");
    this.setState({ playing: false });
  };

  handlePlayPause = () => {
    this.setState({ playing: !this.state.playing });
  };
  handleSeekChange = () => {
    this.p.seekTo(15);
    this.setState({ playing: true });
  };
  ref = (player) => {
    this.player = player;
  };
  render() {
    const {
      url,
      playing,
      controls,
      light,
      volume,
      muted,
      loop,
      played,
      loaded,
      duration,
      playbackRate,
      seconds,
      pip,
    } = this.state;
    const SEPARATOR = " Â· ";

    return (
      <div className="App">
        <div
          style={
            parseFloat(this.state.seconds) < 10 ||
            parseFloat(this.state.seconds) > 11
              ? { display: "none" }
              : {}
          }
          className="button-overlay"
        >
          <div className="buttons">
            <h2>What would you like to do?</h2>
            <div>
              <button
                class="btn"
                value="15"
                onClick={() => this.handleSeekChange()}
              >
                Action 1
              </button>
              <button class="btn" value="20" onClick={() => this.p.seekTo(20)}>
                Action 2
              </button>
            </div>
          </div>
        </div>
        <div className="player-wrapper">
          <ReactPlayer
            ref={(p) => {
              this.p = p;
            }}
            className="react-player"
            url="video.mp4"
            width="100%"
            height="100%"
            onPlay={this.handlePlay}
            onPause={this.handlePause}
            controls={true}
            playing={playing}
            onProgress={(e) => this.handleProgress(e.playedSeconds)}
          />
        </div>
      </div>
    );
  }
}
export default ResponsivePlayer;
